<template>
  <div id="darts-tracker">
    <h1 class="title">Darts Tracker</h1>
    <input class="button add-player" type="text" v-model="name_value" @keypress.enter="submit_player" placeholder="Submit player...">
    <div v-for="n in inc" class="player-container">
      <h4 class="player-name"> Senior {{ player[n-1] }}</h4>
      <darts-player></darts-player>
    </div>
  </div>
</template>

<script>
import DartsPlayer from './components/DartsPlayer.vue';

export default {
  name: 'darts-tracker',
  components: {
    DartsPlayer
  },
  data () {
    return {
      inc: 0,
      player: [],
      name_value: ''
    }
  },
  methods: {
    submit_player: function(event) {
      this.player[this.inc] = this.name_value;
      this.name_value = "";
      this.inc ++;
    }
  }
}
</script>

<style lang="scss">

$background-light-grey: #F5F5F5;
$background-grey: #E8E8E8;

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

body {
  background-color: $background-light-grey;
  font-size: 16px;
  height: 100vh;
  font-family: sans-serif;
}

.button {
  display: block;
  margin-left: auto;
  margin-right: auto;
  font-family: inherit;
  font-size: inherit;
  color: inherit;
  background: none;
  border: 1px solid #0C0;
  cursor: pointer;
  letter-spacing: 1px;
  font-weight: 700;
  outline: none;
  position: relative;

  &.add-player {
    border-radius: 0;
    margin-bottom: 32px;
    border: none;
    border-bottom: 1px solid #0C0;
    padding-left: 0;
    padding-bottom: 8px;

    &:focus {
      border-bottom-width: 2px;
      &::-webkit-input-placeholder {
        opacity: 0;
      }
    }
  }

  &.btn-reseter {
    border: none;
    background-image: url(./assets/refresh-button.svg);
    background-size: cover;
    background-repeat: no-repeat;
    margin-left: 0;
    margin-right: 0;
    text-transform: uppercase;
    width: 25px;
    height: 25px;
  }
}

.score-container {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-justify-content: space-around;
  -ms-flex-pack: distribute;
  justify-content: space-around;
}
.progress {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
  -webkit-justify-content: space-between;
  -ms-flex-pack: justify;
  justify-content: space-between;
}

.progress-bar-container {
  height: 16px;
  width: 180px;
  position: relative;
  border: 1px solid #FF555A;
  background: linear-gradient(90deg, #FF555A, #224D05);
}

.progress-bar {
  height: 14px;
  position: absolute;
  background-color: #ffffff;
  right: 0;
}

.player,
.player-score {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  height: 160px;
}

.player {
  margin-bottom: 24px;
}

.player-score {
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
  -ms-flex-direction: column;
  flex-direction: column;
  -webkit-justify-content: space-around;
  -ms-flex-pack: distribute;
  justify-content: space-around;
  width: 220px;
}

.player-container {
  -webkit-box-align: center;
  -webkit-align-items: center;
  -ms-flex-align: center;
  -ms-grid-row-align: center;
  align-items: center;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
  -ms-flex-direction: column;
  flex-direction: column;
  padding: 16px 0;

  input {
    box-sizing: border-box;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    border: 1px solid #C2C2C2;
    box-shadow: 1px 1px 4px #EBEBEB;
    -moz-box-shadow: 1px 1px 4px #EBEBEB;
    -webkit-box-shadow: 1px 1px 4px #EBEBEB;
    padding: 7px;
    outline: none;
    font-size: 16px;

    &:focus {
      border: 1px solid #0C0;
    }
  }
}

.title {
  text-align: center;
  margin-bottom: 32px;
  margin-top: 16px;
}

.score {
  -webkit-align-self: center;
  -ms-flex-item-align: center;
  align-self: center;
}

.player-container:nth-child(even) {
  background-color: $background-grey;
}

.player-container:nth-child(odd) {
  background-color: $background-light-grey;
}

</style>



