<template>
    <div class="player">
        <div class="player-score">
            <input type="number"
                   v-model="current_value"
                   @keypress.enter="submit"
                   placeholder="Submit score..."
            >
            <div class="progress">
                <div class="progress-bar-container">
                    <div class="progress-bar" :style="myStyle"></div>
                </div>
                <span> {{ remainingPoints }}</span>
            </div>
            <div class="score-container">
                <p class="score">Score: {{ score }}</p>
                <button @click="reseter()" class="button btn-reseter"></button>
            </div>
        </div>
    </div>
</template>

<script>
  export default {
    data: function() {
      return {
        current_value: null,
        score: 0,
        remainingPoints: 301
      }
    },
    methods: {
      submit: function(event) {
        this.current_value = parseInt(this.current_value);
        this.score += this.current_value;
        this.remainingPoints -= this.current_value;
        if (this.score > 301) {
          this.score -= this.current_value;
          this.remainingPoints += this.current_value;
        } else if (this.score == 301) {
          alert("we have a winner");
        }
        this.current_value = null;
      },
      reseter: function(event) {
        this.current_value = 0;
        this.score = 0;
        this.remainingPoints = 301;
      }
    },
    computed: {
      myStyle: function() {
        return {
          width: (100 - (this.score)/3 ) + '%'
        }
      }
    }
  }
</script>



